<script>
import foot from "./public/foot.vue";
import top from "./public/top.vue";
import topMember from "./public/topMember.vue";
export default {
  components: {
    foot,
    topMember,
    top
  },
  data() {
    return {
      priceForm:'',
      vipPriceForm:'',
      member:'',
      tableData: [{
        address: '普通地区',
        weight: '0.5kg内',
        price: 0,
        vipPrice:0,
      }, {
        address: '普通地区',
        weight: '0.5kg-1kg内',
        price: 0,
        vipPrice:0,
      }, {
        address:'普通地区',
        weight: '1kg以上续重',
        price: 0,
        vipPrice:0,
      }, {
        address:'偏远地区',
        weight: '0.5kg内',
        price: 0,
        vipPrice:0,
      }, {
        address:'偏远地区',
        weight: '0.5kg-1kg内',
        price: 0,
        vipPrice:0,
      }, {
        address:'偏远地区',
        weight: '1kg以上续重',
        price: 0,
        vipPrice:0,
      }, {
        date: '偏远地区',
        name: '1kg以上续重',
        price: 0,
        vipPrice:0,
      }]
    };
  },
  mounted(){
    if (sessionStorage.getItem("token") == null) {
      this.$router.push({
        path: "/login"
      });
      return;
    }
    // this.getPrice();
    this.getVipPrice();
  },
  methods: {
    getInfor(data) {
      this.member = data;
    },
    copyUrl(){
      let rl = window.location.href;
      let index= rl.indexOf('.com')+4;
        var input = document.createElement('input');
        input.setAttribute('readonly', 'readonly'); // 防止手机上弹出软键盘
        input.setAttribute('value', rl.substring(0,index)+'/#/?inviteCode='+this.member.inviteCode);
        document.body.appendChild(input);
        // input.setSelectionRange(0, 9999);
        input.select();
        var res = document.execCommand('copy');
        document.body.removeChild(input);
        this.$message({
        type: "info",
        message: `已复制到粘贴板`
      });
    },
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 6) {
          return [2,4];
      }
    },
    getVipPrice(){
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"expressPrice/my",
          "/giftmall/expressPrice/judge",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.priceForm=response.data.data[0];
            that.vipPriceForm=response.data.data[1];
            that.tableData[0].vipPrice = that.vipPriceForm.nearPrice1/100+'元/单';
            that.tableData[1].vipPrice = that.vipPriceForm.nearPrice2/100+'元/单';
            that.tableData[2].vipPrice = that.vipPriceForm.nearPrice3/100+'元/kg';
            that.tableData[3].vipPrice = that.vipPriceForm.farPrice1/100+'元/单';
            that.tableData[4].vipPrice = that.vipPriceForm.farPrice2/100+'元/单';
            that.tableData[5].vipPrice = that.vipPriceForm.farPrice3/100+'元/kg';
            that.tableData[0].price = that.priceForm.nearPrice1/100+'元/单';
            that.tableData[1].price = that.priceForm.nearPrice2/100+'元/单';
            that.tableData[2].price = that.priceForm.nearPrice3/100+'元/kg';
            that.tableData[3].price = that.priceForm.farPrice1/100+'元/单';
            that.tableData[4].price = that.priceForm.farPrice2/100+'元/单';
            that.tableData[5].price = that.priceForm.farPrice3/100+'元/kg';
            let data = that.tableData;
            that.tableData = [];
            that.tableData = data;
          }
        });
    },
    getPrice() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"expressPrice/my",
          "/giftmall/expressPrice/my",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             that.priceForm=response.data.data;
          }
        });
    },
  }
};
</script>

